@use '../../scss/abstract';

.artist-detail {
    padding: 8px;
    padding-top: 0;

    .background {
        position: sticky;
        top: 0;
        left: 0;
        right: 0;
        z-index: 1;
        border-radius: 4px;
        overflow: hidden;
        width: 100%;
        height: clamp(250px, 25vh, 360px);
    }

    .background-img {
        background: rgb(12, 12, 15);
        background: linear-gradient(0deg, rgba(12, 12, 15, 1) 10%, rgba(6, 6, 17, 1) 47%, rgba(15, 24, 25, 1) 72%);
        width: 100%;
        height: 100%;
        // object-fit: contain;
        object-position: center;
        border: none;
    }

    .listener-count {
        margin-top: 12px;
    }

    .verified {
        // @include abstract.flexAlignCenter;
        display: flex;
        align-items: flex-end;
        column-gap: 8px;
        font-weight: 700;
    }

    .info {
        padding: 8px;
        position: absolute;
        bottom: 24px;
        left: 24px;
    }

    .element {
        margin-top: 24px;
    }

    .top-track {
        @include abstract.flexAlignCenter;
        column-gap: 12px;
    }

    .top-value {
        width: 24px;
        font-weight: 700;
    }
    .track-card {
        flex: 1;
        min-width: 0;
    }
}