@use '../../scss/abstract';

.audio-control {
    --control-btn-max-width: 48px;
    height: 100%;
    @include abstract.flexCenter;

    &-top {
        @include abstract.flexCenter;
        column-gap: 16px;
        width: 100%;
    }

    .control {
        @include abstract.flexCenter;
        color: #fff;
        flex: none;
        background: transparent;
        // column-gap: 16px;
        margin-left: 8px;
        margin-right: 8px;
        &[hidden] {
            margin: 0;
        }
        &:hover {
            opacity: 0.9;
        }

        &:disabled,
        &[disabled] {
            color: var(--disabled-color);
        }
    }


    .play-btn {
        @include abstract.flexCenter;
        background: #fff;
        border-radius: 50%;
        color: #000;
        width: 48px;
        height: 48px;

        &.playing {
            padding-left: 0;
        }

        @include abstract.screen(md) {
            width: 32px;
            height: 32px;
        }
    }
}